import {createStore,applyMiddleware} from 'redux';
import promisesMiddleWare from './middlewares/promises';
/*
  state = {
      opened: ...,
      started_animation: ...,
      name:...,
      email:...,
      comment:...
  } 
*/
function reducer(state, action) {
    switch(action.type) {
    case "CHANGING_TO_CLOSE": /* .close_form.click()*/
        {
        console.log("CHANGING_TO_CLOSE");
       let {opened, 
        started_animation, 
        name,email,comment
        } = state;
        let {arrow, forma, my_button, close_form, commentField, emailField,nameField} = action.payload;
        function reverse_step(timestamp) {
            if(!started_animation) started_animation = timestamp;
            var progress = timestamp - started_animation;
            console.log(start);
            forma.style.height = Math.max(170, 370 - progress/2) + 'px';
            my_button.style.top = Math.max(115, 315 - progress/2) + 'px';
            close_form.style.width = Math.max(0, 30 - progress/15) + 'px';
            close_form.style.height = close_form.style.width;
            console.log(forma.style.height);
            if (progress > 0) {
                commentField.style.height = Math.max(0, 110 - progress/2) + 'px';
                }
            if (progress/2 > 110) {
                commentField.style.display = "none";
                }
            if (progress > 300) {
                    emailField.style.display = "none";
                    }
            if (progress/2 + 170 > 370) {
                    nameField.style.display = "none";
                    }
            if (progress < 1000) {
                   
                    window.requestAnimationFrame(reverse_step);
                    }
            
        }
        arrow.style.display = "none";
        /*
        button_text.innerHTML = "Add Contacts";
        button_type.type = "button";
        */
        started_animation = null;
        window.requestAnimationFrame(reverse_step);
        window.history.pushState(0, "title", " ");
        opened = false;
        return {...state, ...{opened, started_animation, name,email,comment}};
    }
    case "CHANGING_FIELDS": {
        let {name,email,comment} = state;
        name = action.name;
        email = action.email;
        comment = action.comment;
        localStorage.setItem("name", action.name);
        localStorage.setItem("email", action.email);
        localStorage.setItem("comment",action.comment);
        return {...state, ...{name, email, comment}};

    }
    case "OPENING_OR_CLOSING": /* .butt3.click()*/
    {
        console.log("OPENING_OR_CLOSING");
        let {opened, 
            started_animation, 
            name,email,comment
            } = state;
        function step(timestamp) {
            let {forma, my_button, close_form, nameField, emailField, commentField, arrow} = action.payload;
            if(!started_animation) started_animation = timestamp;
            let progress = timestamp - started_animation;
            forma.style.height = Math.min(170 + progress/2, 370) + 'px';
            my_button.style.top = Math.min(115 + progress/2, 315) + 'px';
            close_form.style.width = Math.min(progress/15, 30) + 'px';
            close_form.style.height = close_form.style.width;
            if (progress > 0) {
                nameField.style.display = "block";
                }
            if (progress > 80) {
                    emailField.style.display = "block";
            }
            if (progress > 160) {
                commentField.style.display = "block";
                commentField.style.height = Math.min(progress/2 - 80, 110) + 'px';
                }
            if (progress/2 - 80 > 110) {
                    arrow.style.display = "block";
                    }
            if (progress < 1000) {
                        window.requestAnimationFrame(step);
            }
        }
        if(!opened) {
           started_animation = null;
           window.requestAnimationFrame(step);
           opened = true;
           window.history.pushState(1, "title", "#form_link");
           name = localStorage.getItem("name") || "";
           email = localStorage.getItem("email") || "";
           comment = localStorage.getItem("comment") || "";

        }
        else {
            opened = false;
        }
      return {...state, ...{opened, started_animation, name,email,comment}};
    }
      
      case "SUCCESS": 
      {
        console.log("SUCCESS");
        let {non_act, cat, dog, nameField, emailField, commentField} = action.payload;
       
        localStorage.clear();
        nameField.value = "";
        nameField.value = "";
        commentField.value = localStorage.getItem("");
        cat.style.display = "none";
        non_act.style.display = "none";
        dog.style.display = "block";
        var start;
        function step(timestamp) {
        if (!start) start = timestamp;
        var progress = timestamp - start;
        if (progress > 1100) {
            dog.style.display = "none";
        }
        if (progress < 1200) {
            window.requestAnimationFrame(step);
            }
        }
        window.requestAnimationFrame(step);
    return state;
      }
      case "FAILURE":
      {
        console.log("FAILURE");
        let {non_act, cat, dog, nameField, emailField, commentField} = action.payload;
        
        alert("Test error!"); 
        cat.style.display = "none";
            non_act.style.display = "none";
            dog.style.display = "block";
            var start;
            function step(timestamp) {
            if (!start) start = timestamp;
            var progress = timestamp - start;
            if (progress > 1100) {
                dog.style.display = "none";
            }
            if (progress < 1200) {
                window.requestAnimationFrame(step);
                }
            }
            window.requestAnimationFrame(step);
        return state;
        }
      
      case "SENDING": /* .forma.submit()*/
      {
          console.log("SENDING");
          let {non_act, cat, dog, nameField, emailField, commentField} = action.payload;
           non_act.style.display = "block";
          cat.style.display = "block";
        return state;
        }
        default:
            return state;
    }
}
const createStoreWithMiddleWare = applyMiddleware(promisesMiddleWare)(createStore);
const store = createStoreWithMiddleWare(reducer, {
    opened: false,
    started_animation:false,
    name: localStorage.getItem("name") || "",
    email: localStorage.getItem("email") || "",
    comment: localStorage.getItem("comment") || ""
});
export default store;