const middleware = store => next => action => {
    if(action.type !== "PROMISE") {
        return next(action);
    }
    const [startAction, successAction, failureAction] = action.actions;
    store.dispatch({
        type:startAction,
        payload: action.payload
    })
    action.promise.then(() => {
        store.dispatch({
            type:successAction,
            payload:action.payload
        })
    }, (error)=> {
        store.dispatch({
            type:failureAction,
            payload:action.payload
        })
    })

}
export default middleware;