import {get_promises} from  "./api";
export function changeToCloseAction(payload) { /* close_form.click()*/
    return {
        type:"CHANGING_TO_CLOSE",
        payload
    }
}
export function changeFields({name, email, comment}) {
  return {
      type: "CHANGING_FIELDS",
      name,
      email,
      comment
  }
}
export function toggleFormaAction(payload) {
    return {
        type:"OPENING_OR_CLOSING",
        payload
    }
}
export function sendFormaAction(payload) {
    return {
        type:"PROMISE",
        actions:["SENDING", "SUCCESS", "FAILURE"],
        payload,
        promise:get_promises()
    }
}
