import React from 'react';
import Header from './Header';
import Main from './Main';
import Footer from './Footer';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import {changeToCloseAction, toggleFormaAction, sendFormaAction, changeFields} from './actions';
function App(props) {


  
  return (
    <div class="app">
       
       <Header />
       <Main {...props}/>
       <Footer />
         
          
       
    </div>
    
  );

        }


export default connect((state)=>state,
                      (dispatch)=>bindActionCreators({changeToCloseAction, toggleFormaAction, sendFormaAction, changeFields},dispatch))(App);
